import Vue from "vue";
// filter uppercase
Vue.filter("uppercase", function (el) {
  return el.toUpperCase();
});
// filter reverse
Vue.filter("reversing", function (el) {
  return el.split("").reverse().join("");
});
// shorten Text
Vue.filter("shorten", function (el, textLength, suffix) {
  return el.substring(0, textLength) + suffix;
});

Vue.mixin({
  methods: {
    navScroll: function () {
      window.onscroll = function () {
        let mysec = document.querySelector(".header");
        if (window.scrollY >= 100) {
          mysec.classList.add("fixed");
          document.querySelector(".logo").style.color = "#096789";
        } else {
          mysec.classList.remove("fixed");
          document.querySelector(".logo").style.color = "#f4cc2c";
        }
        // let skillsSection = document.querySelector(".skills");
        // let spans = document.querySelectorAll(".the-progress span");
        // // skills
        // if (window.scrollY > skillsSection.offsetTop - 200) {
        //   spans.forEach((span) => {
        //     span.style.width = span.dataset.width;
        //   });
        // }
      };
    },
  },
  mounted: function () {
    this.navScroll();
  },
});
