<template>
  <div class="blog">
    <div class="blog-header">
      <div class="content">
        <h1 class="page-name">{{ pageName }}</h1>
        <p class="page-description">{{ pageDesc }}</p>
      </div>
    </div>
    <div class="blog-content">
      <div class="container">
        <div class="search">
          <input
            class="input"
            type="search"
            placeholder="Search with date, title, author..."
            v-model="search"
          />
          <p class="post-length">Total Blogs: {{ postLength }}</p>
          <p class="result">Result Of Search: {{ filterSearch.length }}</p>
        </div>
        <div class="post-area">
          <blog-post
            v-for="post in filterSearch"
            :key="post.id"
            :views="post.views"
            :title="post.title"
            :date="post.date"
            :content="post.content"
            :author="post.author"
            :category="post.category"
            :length="post.index"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import BlogPost from "@/components/blog/BlogPost.vue";
import BlogJson from "../json/BlogJson.json";
export default {
  data: function () {
    return {
      pageName: "Blog",
      pageDesc: "blogs created with fake APIs",
      posts: BlogJson,
      search: "",
    };
  },
  name: "MyBlog",
  components: {
    BlogPost,
  },
  computed: {
    filterSearch() {
      return this.posts.filter(
        (el) =>
          el.date.includes(this.search) ||
          el.author.includes(this.search) ||
          el.title.includes(this.search)
      );
    },
    postLength() {
      return this.posts.length;
    },
  },
  methods: {
    bgChange() {
      document.querySelector("input").onkeyup = function () {
        if (document.querySelector(".post-area").childElementCount == 0) {
          document.querySelector(".post-area").style.backgroundColor =
            "transparent";
        } else {
          document.querySelector(".post-area").style.backgroundColor = "#eee";
        }
      };
    },
  },
  mounted: function () {
    this.bgChange();
  },
};
</script>
