<template>
  <div class="random-quotes">
    <h2 class="advice-title">Get Advice !</h2>
    <div class="card-box">
      <div class="text">
        <h3>Random Advice</h3>
        <q>{{ qt }}</q>
      </div>
      <div class="line">
        <span class="one"></span>
        <span class="two"><i class="fa-solid fa-quote-right"></i></span>
        <span class="three"></span>
      </div>
      <button @click="newQuote">
        <i class="fa-solid fa-xmark"></i>
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: "RandomQuotes",
  props: ["ql", "quote", "quotes"],
  data() {
    return {
      name: "walid",
      qt: this.quotes[0].quote,
    };
  },
  methods: {
    newQuote: function () {
      const num = Math.floor(Math.random() * this.ql);
      this.qt = this.quotes[num].quote;
    },
  },
};
</script>
