<template>
  <div class="info-page">
    <div class="info-header">
      <h2>Info & Skills</h2>
    </div>

    <div class="container">
      <div class="info">
        <div class="education">
          <h2>Education</h2>
          <p>Bachelor Of Technical</p>
          <p>Applied Degree In Electrical Engineering</p>
          <p>master's degree in renewable energies and energy efficiency</p>
        </div>
      </div>
      <div class="skills">
        <h2>Skills</h2>
        <div class="my-skills">
          <div class="skill">
            <h3>HTML <span>80%</span></h3>
            <div class="the-progress">
              <span style="width: 0" data-width="80%"></span>
            </div>
          </div>
          <div class="skill">
            <h3>CSS <span>85%</span></h3>
            <div class="the-progress">
              <span style="width: 0" data-width="85%"></span>
            </div>
          </div>
          <div class="skill">
            <h3>JavaScript <span>70%</span></h3>
            <div class="the-progress">
              <span style="width: 0" data-width="70%"></span>
            </div>
          </div>
          <div class="skill">
            <h3>Python <span>80%</span></h3>
            <div class="the-progress">
              <span style="width: 0" data-width="80%"></span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "MyInfo",

  methods: {
    skillsData: function () {
      let skillsSection = document.querySelector(".skills");
      let spans = document.querySelectorAll(".the-progress span");
      // skills
      if (window.scrollY >= skillsSection.offsetTop - 300) {
        spans.forEach((span) => {
          span.style.width = span.dataset.width;
        });
      }
    },
  },
  created() {
    window.addEventListener("scroll", this.skillsData);
  },
  destroyed() {
    window.removeEventListener("scroll", this.skillsData);
  },
};
</script>
