import Vue from "vue";
import VueRouter from "vue-router";
import HomeView from "../views/HomeView.vue";
import MyInfo from "../views/info.vue";
import MyBlog from "../views/Blog.vue";
import ContactView from "../views/contact.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "home",
    component: HomeView,
  },

  {
    path: "/info",
    name: "info",
    component: MyInfo,
  },
  {
    path: "/blog",
    name: "blog",
    component: MyBlog,
  },
  {
    path: "/contact",
    name: "contact",
    component: ContactView,
  },
  // {
  //   path: "/about/info",
  //   name: "info",
  //   component: InfoPage,
  // },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
