<template>
  <div class="header-page">
    <div class="picture">
      <img :src="selected" alt="pic" />
    </div>
    <div class="content">
      <h2>Welcome To My Website</h2>
    </div>
    <div class="overlay"></div>
  </div>
</template>

<script>
export default {
  name: "LandingPage",

  data() {
    return {
      images: [
        "https://www.worldtravelguide.net/wp-content/uploads/2017/03/shu-Tunisia-SidiBouSaid-760300645-1440x823.jpg",
        "https://miro.medium.com/max/1400/1*iUaWGAoNEao_JCGMjD40Qw.jpeg",
        "https://www.ceps.eu/wp-content/uploads/2019/12/shutterstock_682701346-1300x731.jpg",
        "https://res.cloudinary.com/dotcom-prod/images/c_fill,f_auto,g_faces:center,q_auto,w_1920/v1/wt-cms-assets/2020/04/lh2fwakxxeyizxh1csqu/countrytunisofficetunisimage1.jpg",
        "https://coworker.imgix.net/pictures/Co222/Tunisia%20resize.jpg?auto=format,compress&fit=clamp",
        "https://scatec.com/wp-content/uploads/sites/7/2021/01/Tunisia_Landscape2.jpg",
        "https://media.tacdn.com/media/attractions-splice-spp-674x446/06/71/dd/36.jpg",
        "https://travelermaster.com/wp-content/uploads/2020/08/maxime-pWP7QqlJq-w-unsplash-scaled.jpg",
      ],
      selected: null,
    };
  },
  methods: {
    randomPic: function () {
      this.selected =
        this.images[Math.floor(Math.random() * this.images.length)];
    },
  },
  mounted: function () {
    this.randomPic();
    setInterval(this.randomPic, 5000);
  },
};
</script>
